.App {
	text-align: center;
	font-family: Arial, sans-serif;
	margin: 20px;
}

.json-input {
	width: 300px;
	height: 100px;
	margin-bottom: 20px;
}

.submit-button {
	display: block;
	margin: 10px auto;
	padding: 10px 20px;
	font-size: 16px;
	background-color: #007bff;
	color: white;
	border: none;
	border-radius: 5px;
	cursor: pointer;
}

.submit-button:hover {
	background-color: #0056b3;
}

.select-menu {
	width: 300px;
	margin: 20px auto;
}

.response-sections {
	text-align: left;
	margin: 20px auto;
	width: 300px;
}

.section {
	margin-bottom: 20px;
}

.section h2 {
	font-size: 18px;
	color: #333;
}

.section pre {
	background-color: #f4f4f4;
	padding: 10px;
	border-radius: 5px;
}
